import React, { useEffect, useState, useContext } from 'react'
import { Form, InputNumber, Row, Col, Button } from 'antd'
import { ModalContext, ColorPicker } from 'src/components'

type Label = {
  label: string,
  width?: number
}
const ItemLabel = (props: Label) => {
  const { label, width = 60 } = props
  return <div style={{ width }}>{label}</div>
}

type Props = {
  zoneStyle?: Object,
  onChange?: Function
}
const ZoneStyle = (props: Props) => {
  const [formBase, setFormBase] = useState({
    backgroundColor: '#ffffff',
    color: '#ffffff',
    fontSize: 14
  })
  useEffect(() => {
    setFormBase(prev => ({ ...prev, ...props.zoneStyle }))
  }, [props.zoneStyle])

  // form 
  const [verify, setVerify] = useState<any>({})
  const validateForm = () => {
    const { fontSize } = formBase
    !fontSize ? (verify.fontSize = 'error') : (verify.fontSize = undefined)
    setVerify({ ...verify })
    return !fontSize
  }

  // 
  const modal = useContext(ModalContext)
  const handleOk = () => {
    if (validateForm()) return

    props.onChange instanceof Function && props.onChange(formBase)
    handleCancel()
  }
  const handleCancel = () => {
    modal.close()
  }

  return (
    <React.Fragment>
      <Form layout='inline'>
        <Row>
          <Col span={24} className='mg-b12'>
            <Form.Item label={<ItemLabel label='' />}>
              <ColorPicker
                color={formBase.backgroundColor}
                onChange={(rgba: string) => setFormBase({ ...formBase, backgroundColor: rgba })}
              />
            </Form.Item>
          </Col>
          <Col span={24} className='mg-b12'>
            <Form.Item label={<ItemLabel label='' />}>
              <ColorPicker
                color={formBase.color}
                onChange={(rgba: string) => setFormBase({ ...formBase, color: rgba })}
              />
            </Form.Item>
          </Col>
          <Col span={24} className='mg-b12'>
            <Form.Item
              label={<ItemLabel label='' />}
              required
              validateStatus={verify.fontSize}
            >
              <InputNumber
                min={12}
                value={formBase.fontSize}
                onChange={(val: number) => setFormBase({ ...formBase, fontSize: val })}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
      <div className='ant-modal-footer'>
        <Button onClick={handleCancel}></Button>
        <Button type='primary' onClick={handleOk}></Button>
      </div>
    </React.Fragment>
  )
}

export default ZoneStyle