import React, { useState, useMemo } from 'react'
import { SettingOutlined } from '@ant-design/icons'
import { EasyModal } from 'src/components'
import { ZoneStyle } from './components'
import { $consts } from 'src/plugins'
import 'src/styles/components/page/zone.less'

type Props = {
  settings: ('Style' | 'Menus')[],
  children: React.ReactChild | React.ReactChild[]
}
const Zone = (props: Props) => {
  const [zoneStyle, setZoneStyle] = useState()

  const enableTools = useMemo(() => {
    const tools: any = {}
    props.settings.forEach((set: string) => {
      tools[set] = true
    })
    return tools
  }, [props.settings])

  return (
    <div className='zone'>
      <div className='zone__content' style={zoneStyle}>
        {props.children}
      </div>
      <div className='zone__tool'>
        {
          enableTools[$consts['COMMON/ZONE_TOOL_STYLE']]
            ? (
              <EasyModal
                title=''
                width='300px'
                maskClosable={false}
                slot={<SettingOutlined title='' />}
              >
                <ZoneStyle zoneStyle={zoneStyle} onChange={setZoneStyle} />
              </EasyModal>
            )
            : null
        }
      </div>
    </div>
  )
}

export default Zone