import { useState, useEffect, useRef } from 'react'
import { Popover } from 'antd'
import { SketchPicker } from 'react-color'
import 'src/styles/components/common/colorPicker.less'

const getSketchRGBA = (rgb: { r: number, g: number, b: number, a: number }) => {
  return `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${rgb.a})`
}

type Props = {
  color: string,
  onChange?: Function
}
const ColorPicker = (props: Props) => {
  const [curr, setCurr] = useState('#ffffff')

  useEffect(() => {
    setCurr(prev => prev !== props.color ? props.color : prev)
  }, [props.color])

  const timer = useRef<any>(-1)
  const handleColorChange = (color: any) => {
    const rgba = getSketchRGBA(color.rgb)
    setCurr(rgba)

    // 
    clearTimeout(timer.current)
    timer.current = setTimeout(() => {
      const { onChange } = props
      onChange instanceof Function && onChange(rgba)
    }, 200)
  }

  return (
    <Popover
      trigger='click'
      content={
        <SketchPicker
          width='240px'
          color={curr}
          onChange={handleColorChange}
        />
      }
      overlayClassName='color-picker'
    >
      <div className='color-picker__color' style={{ backgroundColor: curr }} />
    </Popover>
  )
}

export default ColorPicker